import React from "react";
import {
  Row,
  Col,
  Card,
  InputGroup,
  FormControl,
  Button,
} from "react-bootstrap";

import { Formik } from "formik";
import * as Yup from "yup";
import axios from "axios";

import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
  email: Yup.string().email(i18n.t('error-email')).required(i18n.t('error-required-email')),
  phone_number: Yup.string()
        .matches(phoneRegExp,i18n.t('error-phone-number'))
        .required(i18n.t('error-required-phone-number')),
        firstname: Yup.string()
        .min(2, i18n.t('error-short-first-name'))
        .required(i18n.t('error-required-first-name')),
    lastname: Yup.string()
        .min(2,i18n.t('error-short-last-name'))
        .required(i18n.t('error-required-last-name')),
    address: Yup.string()
        .required(i18n.t('error-address')),
    username: Yup.string()
        .required(i18n.t('error-username')),
    password: Yup.string()
        .required(i18n.t('error-password')),
    Price_colis: Yup.number()
        .typeError(i18n.t('error-colis-price'))
        .min(0,i18n.t('error-positive-colis-price'))
        .required(i18n.t('error-required-colis-price')),
  // city: Yup.string()
  //   .min(2, "City is Too Short!")
  //   .max(20, "City is Too Long!")
  //   .required("City is Required"),
  // country: Yup.string()
  //   .min(2, "Country is Too Short!")
  //   .max(20, "Country is Too Long!")
  //   .required("Country is Required"),
  // firstname: Yup.string()
  //   .min(2, "firstname is Too Short!")
  //   .max(20, "firstname is Too Long!")
  //   .required("firstname is Required"),
  // lastname: Yup.string()
  //   .min(2, "lastname is Too Short!")
  //   .max(20, "lastname is Too Long!")
  //   .required("lastname is Required"),
});
class AddDriver extends React.Component {
  state = {

    privileges: [],
    vehicules: [],
    zones: [],
    permissionss:[],
    showMessageParams:{
      display:false,
      type:"success",
      message:null,                
  },

    };
    handleChange = (event) => {
        console.log(event.target.value);
        this.setState({ permissionss: [...this.state.permissionss, event.target.value]})
    };
  componentDidMount() {
    // axios.get(urlbase+"/privilege/list").then(response=>{


    //   console.log(response.data)
    // })
    const config = {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Headers': 'Authorization',
          "x-access-token": localStorage.getItem('token'),
          'Authorization': 'Bearer '+localStorage.getItem('token'),
        },
      };

    var getprivileges = axios.get(urlbackend + "/privileges", config)
    var vehicules = axios.get(urlbackend + "/vehicule", config)
    var getzones = axios.get(urlbackend + "/zone", config)


    axios
      .all([getprivileges, vehicules, getzones])
      .then(
        axios.spread((...response) => {
          console.log("this respon list0", response[0].data)
          console.log("this respon list1", response[1].data)
          this.setState({ privileges: response[0].data.data })
          this.setState({ vehicules: response[1].data.data.filter((e)=>!(e.status)) })
          this.setState({ zones: response[2].data.data })


        })
      )
      .catch(errors => {
        // react on errors.
        console.error(errors);
      });

  }

  handlSubmit = (values) => {
    this.setState({ 
      showMessageParams:{
          display:false,
          type:"success",
          message:null
          
      }
  });
    values.permissions = this.state.permissionss
    console.log(values);

    const config = {
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Headers': 'Authorization',
          "x-access-token": localStorage.getItem('token'),
          'Authorization': 'Bearer '+localStorage.getItem('token'),
        },
      };

    axios.post(urlbackend + "/AddDriver ", values, config)
      .then(response => {
        this.setState({ 
          showMessageParams:{
              display:true,
              type:"success",
              message:i18n.t('success-add-driver')
          }
      });
        console.log(response.data)

      })
      .catch(e => {
        this.setState({ 
          showMessageParams:{
              display:true,
              type:"danger",
              message:i18n.t('error-add-driver')
          }
      });
        console.log(e)
      })
  };
  showForm = ({
    values,
    errors,
    touched,
    handleChange,
    handleSubmit,
    handleBlur,
    setFieldValue,
    isSubmitting,
    dirty,
    isValid,
    handleReset,
  }) => {
    // console.log(errors);
    const { showMessageParams } = this.state;
    return (
      <Aux>
        <Row>
          <Col>
            <Card>
              <Card.Header>
                <Card.Title as="h5">{i18n.t('add-driver')}</Card.Title>
              </Card.Header>
              {showMessageParams.display==true ?
              <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
              :null
              }
              <Card.Body>
                <Row>
                  <Col md={6}>
                    <CustomInput
                      name="firstname"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.firstname}
                      errors={errors.firstname}
                      touched={touched.firstname}
                      label={i18n.t('first-name')}
                    />

                    <CustomInput
                      name="lastname"
                      label={i18n.t('last-name')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.lastname}
                      errors={errors.lastname}
                      touched={touched.lastname}
                    />
                    <CustomInput
                      name="username"
                      label={i18n.t('username')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.username}
                      errors={errors.username}
                      touched={touched.username}
                    />

                    <CustomInput
                      name="password"
                      label={i18n.t('password')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.password}
                      errors={errors.password}
                      touched={touched.password}
                      type="password"
                    />
                    <CustomInput
                      name="email"
                      label={i18n.t('email')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.email}
                      errors={errors.email}
                      touched={touched.email}
                    />
                    <label htmlFor="basic-url">{i18n.t('zone')}</label>

                    <InputGroup className="mb-3">
                      <FormControl
                        as="select"
                        id="zone"
                        custom
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.Formateur}
                      >
                        <option>{i18n.t('choose-zone')}</option>

                        {this.state.zones.map((zone, index) => (
                          <option key={zone.id} value={zone.id}>
                            {zone.city}:{zone.gouvernorat}
                          </option>
                        ))}
                      </FormControl>
                    </InputGroup>


                  </Col>
                  <Col md={6}>
                    <CustomInput
                      name="phone_number"
                      label={i18n.t('phone-number')}
                      // type="number"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.phone_number}
                      errors={errors.phone_number}
                      touched={touched.phone_number}
                    />
                    <CustomInput
                      name="address"
                      label={i18n.t('address')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.address}
                      errors={errors.address}
                      touched={touched.address}
                    />
                    <label htmlFor="basic-url">{i18n.t('descriminator')}</label>

                    <InputGroup className="mb-3">
                      <FormControl
                        as="select"
                        id="descriminator"
                        custom
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.Formateur}
                      >
                        <option>{i18n.t('choose-descriminator')}</option>

                        {[
                          "DRIVER_CONTRACTER",
                          "DRIVER_INTERNAL",
                        ].map((descriminator, index) => (
                          <option key={descriminator} value={descriminator}>
                            {descriminator}
                          </option>
                        ))}
                      </FormControl>
                    </InputGroup>
                    <label htmlFor="basic-url">{i18n.t('vehicule')}</label>

                    <InputGroup className="mb-3">
                      <FormControl
                        as="select"
                        id="vehicule"
                        custom
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.Formateur}
                      >
                        <option>{i18n.t('choose-vehicule')}</option>

                        {this.state.vehicules.map((privilege, index) => (
                          <option key={privilege.id} value={privilege.id}>
                            {privilege.matricule}
                          </option>
                        ))}
                      </FormControl>
                    </InputGroup>

                    <label htmlFor="basic-url">{i18n.t('privilege')}</label>

                    <InputGroup className="mb-3">
                      <FormControl
                        as="select"
                        id="privilege"
                        custom
                        onChange={handleChange}
                        onBlur={handleBlur}
                        value={values.Formateur}
                      >
                        <option>{i18n.t('choose-privilege')}</option>

                        {this.state.privileges.map((privilege, index) => (
                          <option key={privilege.id} value={privilege.id}>
                            {privilege.name} :{privilege.description}
                          </option>
                        ))}
                      </FormControl>
                    </InputGroup>
                    <CustomInput
                      name="Price_colis"
                      label={i18n.t('price-action')}
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.Price_colis}
                      errors={errors.Price_colis}
                      touched={touched.Price_colis}
                    />
                    {/* <CustomInput
                      name="codeFiscal"
                      label="codeFiscal"
                      onChange={handleChange}
                      onBlur={handleBlur}
                      value={values.codeFiscal}
                      errors={errors.codeFiscal}
                      touched={touched.codeFiscal}
                    /> */}

                  </Col>
                </Row>

                <Button
                disabled={Object.keys(errors).length>0}
                  onClick={() => {
                    this.handlSubmit(values);
                    handleReset();
                    // console.log(values);
                  }}
                >
                  {i18n.t('save')}
                </Button>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </Aux>
    );
  };

  render() {
    return (
      <Formik
        initialValues={{
          firstname: "",
          lastname: "",
          username: "",
          password: "",
          email: "",
          phone_number: "",
          address: "",
          Price_colis:0
        }}
        onSubmit={(values, { setSubmitting }) => {
          this.submitForm(values);
          setSubmitting(false);
        }}
        validationSchema={SignupSchema}
      >
        {(props) => this.showForm(props)}
      </Formik>
    );
  }
}

export default AddDriver;
